.side_tab{
    position: fixed;
    background-color: rgb(32, 29, 29);
    width: 18%;
    height: 92%;
    top: 8%;
    z-index: 2;
    transition: transform 500ms ease-in-out;
    color: #fff;
    padding: 1vw 0;
  }
  .side_tab.hid{
    transform: translateX(-50vw);
    transition: transform 500ms ease-in-out;
  }
  .side_tab h2{
    margin-bottom:4vh;
    font-size: 4vh;
    line-height: 5vh;
  }
  .side_tab label{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    line-height: 6vh;
    padding: 0 1vh;
    font-size: 2.8vh;
  }
  .side_tab label input{
    width: 90%;
    height: 4vh;
    border-radius: 1vh;
    outline: none;
    padding: 1vh;
    font-size: 1.6vh;
  }
  .side_tab button{
    width: 16vh;
    height: 5vh;
    margin: 4vh 0 0 0;
    border-radius: 2vh;
    outline: none;
    cursor: pointer;
    background-color: #00a2ff;
    color: #fff;
    font-size: 2.5vh;
  }
  .side_tab ul{
    display: grid;
    grid-template-columns: repeat(2,0fr);
    justify-content: space-around;
    margin: 5vh 0 2vh 0;
    list-style: none;
  }
  .side_tab ul li{
    background-color: rgba(0, 0, 0, 0.226);
    width: 18vh;
    height: 12vh;
    margin: 2vh 0;
    border-radius: 2vh;
    border: 2px groove #fff;
    color: #fff;
    line-height: 4vh;
    font-size: 1.5vh;
  
  }
  .side_tab ul li p:nth-child(1){
    color: #00a2ff;
  }
  .side_tab ul li p:nth-child(2){
    font-size: 1.8vh;
  }
  .side_tab ul li p:nth-child(3){
    color:#857aff;
  }
  .side_tab h4{
    text-align: start;
    margin: 6vh 0vh 0 1vh;
    color: #E8E069;
    display: flex;
    align-items: center;
  }
  .side_tab h4 img{
    width: 2vh;
    height: 2vh;
    margin: 0 1vh;
    filter: invert(90%) sepia(46%) saturate(568%) hue-rotate(348deg) brightness(97%) contrast(87%);
  }
  .menu-icon{
    position: fixed;
    width: 3vw;
    height: 6vh;
    top: 50%;
    left: 18%;
    transition: transform 400ms ease-in-out;
    filter: invert(8%) sepia(97%) saturate(4478%) hue-rotate(287deg) brightness(89%) contrast(104%);
    cursor: pointer;
    z-index: 2;
  }
  .menu-icon.back{
    transform: translateX(-18vw);
    transition: transform 400ms ease-in-out;
  }

  /* Mobile Styling */

  .side_tab.mob{
    width: 92%;
    z-index: 5;
    transition: transform 500ms ease-in-out;
  
  }
  .side_tab.mob.hid{
    transform: translateX(-100vw);
    transition: transform 500ms ease-in-out;
  }
  .menu-icon.mob{
    width: 4vh;
    height: 4vh;
    top: 50%;
    left: 0%;
    transition: transform 400ms ease-in-out;
  
  }
  .menu-icon.mob.back{
    transform: translateX(92vw);
    transition: transform 400ms ease-in-out;
  }