.countdown {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 5%;
  bottom: 10%;
}
.countdown-item {
    width: 7vw;
    height: 12vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-image: linear-gradient(0deg, #000 0%, #444 100%);
    color: #fff;
    font-size: 1.6vw;
    border-radius: 1vh;
    margin: 0 1vh;
    z-index: 2;
}

.pulse {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 0;
  height: 0;
  opacity: 1;
  background-color: #888;
  border-radius: 50%;
  z-index: 1;
  /* animation: pluseAnimation 1s ease infinite; */
}
