.home{
  color: #000;
  user-select: none;
  text-align: center;

}
.appbar{
  width: 100%;
  height: 8vh;
  background-color: rgb(32, 29, 29);
  position: fixed;
  z-index: 3;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
.appbar ul{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 50%;
  height: 100%;
  float: right;
  list-style: none;
}
.appbar ul li{
  width: 5vw;
  height: 100%;
  display: flex;
  transition: 100ms ease-in-out;
}
.appbar ul li a{
  color: #fff;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8vh;
}
.appbar ul ul li a img{
  width: 1.5vw;
  margin: 0 1vw;
  cursor: pointer;
}

.appbar ul li:hover{
  background-color: #03e1ff;
}
.appbar ul ul{
  width: fit-content;
}
.appbar ul ul li:hover{
  background-color: transparent;
  transform: scale(1.2);
}

/* .appbar ul li:last-child:hover,.appbar ul li:nth-child(6):hover{
  background-color: transparent;
} */
.background{
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(to left bottom, #00ffa3, #00fcc1, #00f7d9, #00f2eb, #00ebf6, #00e1fb, #00d7fe, #00cdff, #00bcff, #00a2ff, #857aff, #dc1fff);
}
.logo{
  position: absolute;
  width: 15vw;
  height: 30vh;
  left: 76%;
  bottom: 5%;
  z-index: 2;
  animation: logo 500ms linear 1;

}
.background h1{
  position: absolute;
  left: 3%;
  top: 25%;
  font-size: 8vh;
  animation: tit 500ms linear 1;
  color: #fff;
}
.background h3{
  position: absolute;
  left: 10%;
  top: 43%;
  font-size: 3.5vh;
  animation: slog 500ms linear 1;
  color: #fff;
  display: flex;
  align-items: center;
}
.background span{
  position: absolute;
  left: 50%;
  top: 50%;
  font-size: 10vh;
  animation: slog 500ms linear 1;
}
.background h3 img{
  width: 4vh;
  height: 4vh;
  margin-left: 1vh;
}
@keyframes tit {
  0%{
    transform: translateY(-50vh);
    visibility: visible;
  }
  100%{
    transform: translateY(0);
    visibility: visible;
  }
}
@keyframes slog {
  0%{
    transform: translateX(-20vh);
    visibility: visible;
  }
  100%{
    transform: translateX(0);
    visibility: visible;
  }
}
@keyframes logo {
  0%{
    transform: translateY(-50vh);
    visibility: visible;
  }
  100%{
    transform: translateY(0);
    visibility: visible;
  }
}

.launch{
  position: absolute;
  background-image: linear-gradient(0deg, rgb(209, 209, 209) 0%, #444 100%);
  width: fit-content;
  bottom: 10%;
  left: 10%;
  color: #fff;
  font-size: 5vh;
  line-height: 10vh;
  padding: 0 2vw;
  text-transform: uppercase;
  border-radius: 2vh;
  animation: shine 1s linear infinite;
}

.launch h2{
  background: linear-gradient(to right, #dc1fff 20%, #00ffa3 40%, #03e1ff 60%, #101921 80%);
  background-size: 200% auto;
  color: #000;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 2s linear infinite;
}
@keyframes shine {
  to {
    background-position: 200% center;
  }
}
/* -------- */

.intro{
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 6vh 6vw;
  margin-bottom: 5vh;
}
.intro section{
  width: 100%;
  border-bottom: .3vh groove #000;
}
.intro h3, .clan h3{
  padding: 2vh 0;
}
.intro img{
  width: 15vh;
  padding: 2vh 0;
}
.intro h1{
  width: 50%;
  max-width: 80vw;
  margin: 5vh auto;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 2vh;
  padding: 3vh;
  line-height: 6vh;
  font-size: 3.7vh;
}
.intro h1 h4{
  font-size: 2vh;
  font-weight: 500;
}
.intro h5, .clan h5{
  line-height: 6vh;
  font-weight: 300;
}
.tokens{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 6vh 0;
}
.tokens .nft_img{
  text-align: center;
}
.nft{
  line-height: 6vh;
}
.nft img{
  width: 25vw;
  height: 50vh;
  border-radius: 50%;
  object-fit:fill;
  margin-bottom: 2vh;
}
.nft h4{
  font-size: 2.5vh;
}
.mint{
  width: 90%;
  margin: 5vh auto;
  text-align: center;
} 
.mint a{
  color: #000;
  padding: 1vh 2vw;
  border-radius: 2vh;
  font-size: 3vh;
  box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
  background-color: #cdb088;
  pointer-events: none;
  cursor: default;
  opacity: 0.5;
}
.mint a:hover{
  background-color: rgb(167, 135, 92)
}
.story{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.story img{
  width: 80%;
  height: 60vh;
  padding: 5vh 0;
  margin: 5vh 0;
}
.story p{
  width: 70%;
  padding: 3vh 0;
  line-height: 5vh;
  font-size: 1.8vh;
}
.story a{
  width: 15vw;
  height: 7vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5vh;
  font-weight: bold;
  text-decoration: none;
  border: .5vh groove #cc6059;
  border-radius: 3vw;
  color: #cc6059;
  margin: 4vh 0;
}
.clan{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 6vh 6vw;
  border-bottom: .3vh groove #000;
}
.clan img{
  width: 100%;
  height: 80vh;
  padding: 5vh 0;
  object-fit:fill;
}
.roadmap{
  width: 90%;
  margin: 0vh auto;
  border-bottom: .3vh groove #000;
}
.steps{
  min-height: 90vh;
  margin: 10vh auto;
}
.steps div{
  position: absolute;
}
.steps div img{
  position: absolute;
  width: 3vw;
  filter: invert(32%) sepia(68%) saturate(416%) hue-rotate(349deg) brightness(101%) contrast(89%);
}
.steps div h3{
  width: 20vw;
  height: 20vh;
  padding: 1vw;
  background-color: #cdb088;
  border-radius: 2vh;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  line-height: 6vh;
  font-weight: 300;

}
.steps div h3 p{
  font-size: 1.6vh;
}
.steps div:nth-child(1){
  left: 5%;
}
.steps div:nth-child(2){
  left: 38%;
  margin-top: 22vh;
}
.steps div:nth-child(3){
  left: 75%;
  margin-top: 5vh;
}
.steps div:nth-child(4){
  right: 10%;
  margin-top: 65vh;
  opacity: .5;

}
.steps div:nth-child(5){
  left: 30%;
  margin-top: 70vh;
  opacity: .5;

}
.steps div:nth-child(6){
  left: 1%;
  margin-top: 40vh;
  opacity: .5;

}
/* div 1 */
.steps div:nth-child(1) img:nth-child(2){
  transform: rotateZ(110deg);
  left: 100%;
}
.steps div:nth-child(1) img:nth-child(3){
  left: 130%;
  transform: rotateZ(110deg);
  margin-top: 7vh;
}
/* div 2 */
.steps div:nth-child(2) img:nth-child(2){
  transform: rotateZ(50deg);
  left: 105%;
  margin-top: -15vh;
}
.steps div:nth-child(2) img:nth-child(3){
  left: 125%;
  transform: rotateZ(60deg);
  margin-top: -22vh;
}
.steps div:nth-child(2) img:nth-child(4){
  left: 150%;
  transform: rotateZ(90deg);
  margin-top: -30vh;
}

/* div 3 */
.steps div:nth-child(3) img:nth-child(2){
  transform: rotateZ(180deg);
  left: 50%;
  margin-top: 2vh;
  opacity: .5;

}
.steps div:nth-child(3) img:nth-child(3){
  transform: rotateZ(230deg);
  left: 50%;
  margin-top: 15vh;
  opacity: .5;

}
.steps div:nth-child(3) img:nth-child(4){
  transform: rotateZ(220deg);
  left: 30%;
  margin-top: 25vh;
  opacity: .5;

}
/* div 4 */
.steps div:nth-child(4) img:nth-child(2){
  transform: rotateZ(240deg);
  left: -30%;
  margin-top: -15vh;
  opacity: .5;
}
.steps div:nth-child(4) img:nth-child(3){
  transform: rotateZ(270deg);
  left: -75%;
  margin-top: -10vh;
  opacity: .5;

}
/* div 5 */
.steps div:nth-child(5) img:nth-child(2){
  transform: rotateZ(300deg);
  left: -25%;
  margin-top: -10vh;
  opacity: .5;

}
.steps div:nth-child(5) img:nth-child(3){
  transform: rotateZ(300deg);
  left: -60%;
  margin-top: -15vh;
  opacity: .5;

}
.steps div:nth-child(5) img:nth-child(4){
  transform: rotateZ(0deg);
  left: -85%;
  margin-top: -27vh;
  opacity: .5;

}
/* div 6 */
.steps div:nth-child(6) img:nth-child(2),
.steps div:nth-child(6) img:nth-child(3){
  visibility: hidden;
}
/* ----------------- */
.team{
  width: 90%;
  margin: 15vh auto;
  border-bottom: .3vh groove #000;
  padding-bottom: 10vh;
}
.members{
 display: grid;
 grid-template-columns: repeat(4,0fr);
 justify-content: space-around;
 
}
.members div img{
  width: 18vw;
  height: 39vh;
  margin: 5vh 0 0 0;
}
.partner{
  width: 90%;
  display: grid;
  grid-template-columns: repeat(3,30fr);
  justify-content: center;
  margin: 5vh auto 30vh auto;
  height: 40vh;
  border-radius: 2vh;
  padding: 5vh;
}
.partner a{
  width: 15vw;
  height: 10vh;

}
.partner a img{
  width: 15vw;
  height: 22vh;
  margin: 1vh 1.5vw;
}
.ftr{
  width: 100%;
  height: 100%;
  background-color: rgb(32, 29, 29);
}
.t-ftr{
  height: 30vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-bottom: .2vh solid #ffff;
  padding: 3vh 0;
}
.sections-ftr,.socials-ftr{
  width: 15vw;
  color: #03e1ff;
}
.logo_ftr img{
  width: 10vw;
  height: 100%;
  border-radius: 50%;

}
.sections-ftr li, .socials-ftr li{
  margin: 3vh 0;
  list-style: none;
}
.sections-ftr li a, .socials-ftr li a{
  color: #fff;
  font-size: 1.8vh;
}
.b-ftr{
  padding: 1vh 0;
  text-align: center;
  color: #fff;
}


/* ----------- Mobile Styling ------------------*/
.appbar.mob{
  border-radius: 0vh 0vh 2vh 2vh;
}
.appbar.mob ul{
  width: 100%;
  float:left
}
.appbar.mob ul li{
  width: 100%;
}
.appbar.mob ul ul{
  margin-right: 2vw;
}
.appbar.mob ul ul li a img{
  width: 5vw;
}
.intro.mob h1{
  width: 100%;
}
.background.mob{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: lightcoral;
}
.background.mob h1{
  position: unset;
  font-size: 5vh;
}
.background.mob h3{
  position: unset;
  font-size: 2.5vh;
}
.background.mob span{
  left: 43%;
  top: 65%;
  font-size: 3vh
}
.logo.mob{
  position:unset;
  width: 25vw;
  height: 12.5vh;
  z-index: 2;
  margin-bottom: 7vh;
  
}
.dog.mob{
  display: none;
}
.clan.mob img{
  width: 100vw;
  height: 35vh;
}
.tokens.mob{
  flex-direction: column;
  width: 100%;
}
.nft_img.mob img{
  width: 100vw;
  height: 50vh;
  margin: 5vh 0;
  border-radius: 50%;
}
.members.mob{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  
}
.members.mob div img{
   width: 70vw;
   height: 35vh;
   margin: 5vh 0 0 0;
}
.nft.mob img{
  width: 90vw;
  height: 50vh;
  margin: 5vh 0;
  border-radius: 50%;
}

.steps.mob{
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100%;
}
.steps.mob div{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 25vh;
  position: relative;
  left: 0;
  margin: 7vh 0;
}
.steps.mob div img{
  position: relative;
  width: 12vw;
  left: 0;
  margin-top: 0;
  margin-left: 0;
}
.steps.mob div h3{
  width: 90vw;
  height: 100%;
  padding: 1vw;
  line-height: 6vh;
  font-weight: 700;
}
.steps.mob div h3 p{
  font-size: 2vh;
  font-weight: 300;
}
.steps.mob div:nth-child(odd) img:nth-child(odd),
.steps.mob div:nth-child(even) img:nth-child(even),
.steps.mob div:nth-child(odd) img:nth-child(even),
.steps.mob div:nth-child(even) img:nth-child(odd){
  transform: rotateZ(180deg);
  margin-top: 0;
  left: 0;
  margin-top: 10vh;
}
.steps.mob div img:nth-child(2),
.steps.mob div img:nth-child(4){
  display: none;

}
.mint.mob{
  width: 100%;
} 
.mint.mob a{
  text-decoration: none;
  color: #000;
  border: .3vh groove #000;
  padding: 1vh 2vw;
  border-radius: 2vh;
  font-size: 2.5vh;
}
.partner.mob{
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0 auto;
}
.partner.mob a{
  width: 15vh;
  height: 10vw;

}
.partner.mob a img{
  width: 25vh;
  height: 35vw;
  margin: 2vh 1.5vh;
  background-color: #600570;

}
.partner.mob div:nth-child(5) a img,
.partner.mob div:nth-child(6) a img
{
  width: 25vh;
  height: 20vw;
  background-color: #600570;
}
.t-ftr.mob{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 2vh 0 0 0;
  height: 100%;
}
.logo_ftr.mob{
  width: 17vh;
  height: 17vh;;
}
.logo_ftr.mob img{
  width: 100%;
  height: 100%;
}
.sections-ftr.mob,.socials-ftr.mob{
  width: 30vw;
  margin-top: 3vh;
}

.launch.mob{
  width: 90%;
  bottom: 1%;
  left: 5%;
  color: #fff;
  font-size: 3vh;
  line-height: 10vh;
  padding: 0 2vw;
  text-transform: uppercase;
  border-radius: 2vh;
}